import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController,
    AlertController } from 'ionic-angular';
import { FormBuilder, Validators } from '@angular/forms';
import { EmailValidator } from '../../validators/email';
import { AuthData } from '../../providers/auth-data';
import {HomePage}from'../home/home';

import { ResetPassword } from '../reset-password/reset-password';
/*

 * Generated class for the Login page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@Component({
    selector: 'page-login',
    templateUrl: 'login-email.html',
})
export class LoginEmailPage {
    public loginEmailForm;
    loading: any;

    constructor(public navCtrl: NavController, public navParams: NavParams
        , public formBuilder: FormBuilder,
        public alertCtrl: AlertController, public loadingCtrl: LoadingController,
        public authData: AuthData, public nav: NavController) {

        this.loginEmailForm = formBuilder.group({
            email: ['', Validators.compose([Validators.required, EmailValidator.isValid])],
            password: ['', Validators.compose([Validators.minLength(6), Validators.required])]
        });

    }

    loginEmailUser(): void {
        if (!this.loginEmailForm.valid) {
            console.log(this.loginEmailForm.value);
        } else {
            const email = this.loginEmailForm.value.email;
            this.authData.loginUser(email, this.loginEmailForm.value.password).then(authData => {
                this.loading.dismiss().then(() => {
                    this.nav.setRoot(HomePage);
                });
            }, error => {
                this.loading.dismiss().then(() => {
                    let alert = this.alertCtrl.create({
                        message: error.message,
                        buttons: [
                            {
                                text: "Ok",
                                role: 'cancel'
                            }
                        ]
                    });
                    alert.present();
                });
            });

            this.loading = this.loadingCtrl.create();
            this.loading.present();
        }
    }

    goToResetPassword(): void {
        this.nav.push(ResetPassword);
    }
}



