import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { AuthData } from '../../providers/auth-data';
import { AngularFireAuth } from 'angularfire2/auth';
import { Login } from '../login/login';
import { ToastController } from 'ionic-angular';
import { SelectQuarterPage } from '../select-quarter/select-quarter';
import { AccountSettingsPage } from '../account-settings/account-settings';
import { SubjectAveragePage } from '../subject-average/subject-average';
import * as firebase from 'firebase/app';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  fullname:string;
    constructor(public navCtrl: NavController, public afAuth: AngularFireAuth, public toast: ToastController, public authData: AuthData) {

  }

  ionViewWillLoad() {
    this.afAuth.authState.subscribe(data => {
      this.toast.create({
        message: 'Successfully connected.',
        duration: 4000
      }).present();
    });
  let fullname;
  var ref = firebase.database().ref("Students");
       ref.on('value', snap => {
         this.fullname = snap.child(firebase.auth().currentUser.uid + '/fullName').val();
         console.log(this.fullname);
       });
 }
  openSelectQuarterPage() {
  	this.navCtrl.push(SelectQuarterPage);
  }

  openSettingsPage() {
    this.navCtrl.push(AccountSettingsPage);
  }

  openSubjectAveragePage() {
    this.navCtrl.push(SubjectAveragePage);
  }

  logOut() {
      this.authData.logoutUser().then(() => {
          this.navCtrl.setRoot(Login);
      });
  }

}



