import { Injectable } from '@angular/core';
import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { AngularFireDatabase } from 'angularfire2/database';
import * as firebase from 'firebase/app';
import { AngularFireAuth } from 'angularfire2/auth';

export class Item {
	body: string;
}

@Injectable()
@Component({
  selector: 'page-grades',
  templateUrl: 'grades.html',
})
export class GradesPage {

	quarter;
	subject;
	items: Array<any> = [];
	keys: Array<any> = [];

	constructor(public navCtrl: NavController, public navParams: NavParams, private afAuth: AngularFireAuth) {
		this.afAuth.authState.subscribe(user => {
			if(user) console.log(user.uid);

		})
	}
	
	ionViewDidLoad() {
			
		this.quarter = this.navParams.get('quarter');
		this.subject = this.navParams.get('subject');
 		this.items = [];
	    this.keys = [];

	    let sectionCode;
	    let section;
	    let mySubject = this.subject;
	    let myQuarter = this.quarter;
	    let myItem = this.items;
 		var ref= firebase.database().ref("Students");
 			ref.on('value', snap => {
			    sectionCode = snap.child(firebase.auth().currentUser.uid + '/sectionCode').val();
			    console.log(sectionCode); //

			    var studRef = firebase.database().ref().child('Sections').orderByChild('sectionCode').equalTo(sectionCode);

			    studRef.on('value', snapshot =>{
				snapshot.forEach(userSnapshot => {
				    var chuchu = userSnapshot.val();
				    console.log(chuchu.secName);
				    section = chuchu.secName;
				        //DAta all together reference	
			 	var itemRef = firebase.database().ref('/' + mySubject + '/' + section + '/' + myQuarter + '/' + firebase.auth().currentUser.uid);
			 	console.log(myQuarter + mySubject + section + firebase.auth().currentUser.uid);

				itemRef.on('value', itemSnapshot => {
			    
				    itemSnapshot.forEach( itemSnap => {
				      var itemSnapKey = itemSnap.key.replace('!','');
				      myItem.push({key:itemSnapKey,value:itemSnap.val()});

				      return false;
				    });
			  	});
			  	return false;
			});

			    
			  });
		  });
		//var itemRef = firebase.database().ref('AP-Grades/Section-7A/w1FqmxkmWTc8dxLzNnblWleYoU93/First-Quarter');
		
	}
	
}





