import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { SelectSubjectPage } from '../select-subject/select-subject';
import { SummaryPage } from '../summary/summary';
/**
 * Generated class for the SelectQuarterPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-select-quarter',
  templateUrl: 'select-quarter.html',
})
export class SelectQuarterPage {

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  openSelectSubjectPage(value :any) {
    let data1 = {
      quarter: value
    }
  	this.navCtrl.push(SelectSubjectPage, data1);
  }

   openSummaryPage() {
    this.navCtrl.push(SummaryPage);
  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad SelectQuarterPage');
  }

}



