import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import * as firebase from 'firebase/app';
/**
 * Generated class for the SubjectSummaryPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-subject-summary',
  templateUrl: 'subject-summary.html',
})
export class SubjectSummaryPage {

  subject;

  first: Array<any> = [];
  second: Array<any> = [];
  third: Array<any> = [];
  fourth: Array<any> = [];
  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SubjectSummaryPage');
    this.subject = this.navParams.get('subject');
    let sectionCode;
    let section;
    let mySubject = this.subject;
	let myFirst = this.first;
	let mySecond = this.second;
	let myThird = this.third;
	let myFourth = this.fourth;
 		var ref = firebase.database().ref("Students");
				ref.on('value', function(snap) {
			    sectionCode = snap.child(firebase.auth().currentUser.uid + '/sectionCode').val();
			    console.log(sectionCode); //

			    var studRef = firebase.database().ref().child('Sections').orderByChild('sectionCode').equalTo(sectionCode);

			    studRef.on('value', function(snapshot){
				snapshot.forEach(userSnapshot => {
				    var username = userSnapshot.val();
				    console.log(username.secName);
				    section = username.secName;

			 	var firstRef = firebase.database().ref('/' + mySubject + '/' + section + '/First-Quarter/' + firebase.auth().currentUser.uid );
			 	var secondRef = firebase.database().ref('/' + mySubject + '/' + section + '/Second-Quarter/' + firebase.auth().currentUser.uid);
			 	var thirdRef = firebase.database().ref('/' + mySubject + '/' + section + '/Third-Quarter/' + firebase.auth().currentUser.uid);
			 	var fourthRef = firebase.database().ref('/' + mySubject + '/' + section + '/FourthQuarter/' + firebase.auth().currentUser.uid);

				console.log(mySubject + section + firebase.auth().currentUser.uid);

				firstRef.on('value', itemSnapshot => {
			    itemSnapshot.forEach( itemSnap => {
			      var itemSnapKey = itemSnap.key.replace('!','');
			      myFirst.push({key:itemSnapKey,value:itemSnap.val()});

			      return false;
			    });
			  });

				secondRef.on('value', itemSnapshot => {
			    itemSnapshot.forEach( itemSnap => {
			      var itemSnapKey = itemSnap.key.replace('!','');
			      mySecond.push({key:itemSnapKey,value:itemSnap.val()});

			      return false;
			    });
			  });	

			  	thirdRef.on('value', itemSnapshot => {
			    itemSnapshot.forEach( itemSnap => {
			      var itemSnapKey = itemSnap.key.replace('!','');
			      myThird.push({key:itemSnapKey,value:itemSnap.val()});

			      return false;
			    });
			  });

			  fourthRef.on('value', itemSnapshot => {
			    itemSnapshot.forEach( itemSnap => {
			      var itemSnapKey = itemSnap.key.replace('!','');
			      myFourth.push({key:itemSnapKey,value:itemSnap.val()});

			      return false;
			    });
			  });	  
  
			  return false;


			});
		});
  	});

}
}


