import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { SubjectSummaryPage } from '../subject-summary/subject-summary';
import * as firebase from 'firebase/app';
/**
 * Generated class for the SummaryPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-summary',
  templateUrl: 'summary.html',
})
export class SummaryPage {

  subjects: Array<any> = [];

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  openSubjectSummaryPage(value :any) {
  	let data3 = {
      subject: value
    }
    this.navCtrl.push(SubjectSummaryPage, data3);
  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad SummaryPage');
    let sectionCode;
    let subject;
    let mySub = this.subjects;

    var ref= firebase.database().ref("Students");
       ref.on('value', function(snap) {
          sectionCode = snap.child(firebase.auth().currentUser.uid + '/sectionCode').val();
          console.log(sectionCode); //

          var subRef = firebase.database().ref().child('Subjects').orderByChild('sectionCode').equalTo(sectionCode);

          subRef.on('value', snapshot => {
            snapshot.forEach(userSnapshot => {
                var username = userSnapshot.val();
                console.log(username.subjectName);
                subject = username.subjectName;
                mySub.push({value:subject});
            return false;
          });
          
      });
  });
  }

}



