import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import * as firebase from 'firebase/app';
import { SlicePipe } from '@angular/common';
/**
 * Generated class for the SubjectAveragePage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-subject-average',
  templateUrl: 'subject-average.html',
})
export class SubjectAveragePage {

  subjects: Array<any> = [];
  firsts: Array<any> = [];
  seconds: Array<any> = [];
  thirds: Array<any> = [];
  fourths: Array<any> = [];
  averages: Array<any> = [];
  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    let sectionCode;
    let section;
    let subject;
    let average = this.averages;
    let first = this.firsts;
    let second = this.seconds;
    let third = this.thirds;
    let fourth = this.fourths;
    let mySub = this.subjects;

    var ref = firebase.database().ref("Students");
        ref.on('value', snap => {
          sectionCode = snap.child(firebase.auth().currentUser.uid + '/sectionCode').val();
          console.log(sectionCode); //

          var secRef = firebase.database().ref().child('Sections').orderByChild('sectionCode').equalTo(sectionCode);
          secRef.once('child_added', snapshot => {
                section = snapshot.child('secName').val();
                      console.log(section);

                  var subRef = firebase.database().ref().child('Subjects').orderByChild('sectionCode').equalTo(sectionCode);
                  subRef.on('child_added', takesnapshot => {
                      subject = takesnapshot.child('subjectName').val();
                      console.log(subject);
                      mySub.push({name:subject});

         var firstRef = firebase.database().ref('/' + subject + '/' + section + '/First-Quarter/' + firebase.auth().currentUser.uid);
         var secondRef = firebase.database().ref('/' + subject + '/' + section + '/Second-Quarter/' + firebase.auth().currentUser.uid);
         var thirdRef = firebase.database().ref('/' + subject + '/' + section + '/Third-Quarter/' + firebase.auth().currentUser.uid);
         var fourthRef = firebase.database().ref('/' + subject + '/' + section + '/Fourth-Quarter/' + firebase.auth().currentUser.uid);
         
         var qtsb1 = subject + ' 1st Qtr';
         var qtsb2 = subject + ' 2nd Qtr';
         var qtsb3 = subject + ' 3rd Qtr';
         var qtsb4 = subject + ' 4th Qtr';

         firstRef.once('value', snapshot => {
          var item = snapshot.child('QGRADE').val();
          
          average.push({average:item, quarter:qtsb1});
          console.log(item);

          });
        
        secondRef.once('value', snapshot => {
           var item = snapshot.child('QGRADE').val();
           
            average.push({average:item, quarter:qtsb2});
            console.log(item);
        });
        thirdRef.once('value', snapshot => {
           
           var item = snapshot.child('QGRADE').val();
           
           average.push({average:item, quarter:qtsb3});
           console.log(item);
        });
        fourthRef.once('value', snapshot => {
          var item = snapshot.child('QGRADE').val();
          
          average.push({average:item, quarter:qtsb4});
          console.log(item);        
        });
        
        return false;
      });  
console.log(average);
return false;
        });
     });
  }

  start: number = 0;
  end: number = 4;

}



