import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, AlertController } from 'ionic-angular';
import { HomePage } from '../home/home';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { EmailValidator } from '../../validators/email';
import { AuthData } from '../../providers/auth-data';
import { AngularFireAuth } from 'angularfire2/auth';
import { ToastController } from 'ionic-angular';
import * as firebase from 'firebase/app';
/**
 * Generated class for the AccountSettingsPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-account-settings',
  templateUrl: 'account-settings.html',
})
export class AccountSettingsPage {
  public newPassForm;
  public emailForm;
  loading: any;

  constructor(public navCtrl: NavController, public navParams: NavParams, public formBuilder: FormBuilder,
        public alertCtrl: AlertController, public toast: ToastController, public loadingCtrl: LoadingController,
        public authData: AuthData, public nav: NavController) {

  			this.newPassForm = formBuilder.group({
            password: ['', Validators.compose([Validators.minLength(6), Validators.required])],
            confirmPassword: ['', Validators.required],
    }, {validator: this.matchingPasswords('password', 'confirmPassword')});

        this.emailForm = formBuilder.group({
            email: ['', Validators.compose([Validators.required, EmailValidator.isValid])]
        });
  }

  matchingPasswords(passwordKey: string, confirmPasswordKey: string) {
    // TODO maybe use this https://github.com/yuyang041060120/ng2-validation#notequalto-1
    return (group: FormGroup): {[key: string]: any} => {
      let password = group.controls[passwordKey];
      let confirmPassword = group.controls[confirmPasswordKey];

      if (password.value !== confirmPassword.value) {
        return {
          mismatchedPasswords: true
        };
      }
    }
  }
  changeEmail(): void {
    var user = firebase.auth().currentUser;
          user.updateEmail(this.emailForm.value.email).then(user=> {
                this.loading.dismiss().then(() => {
                    this.nav.setRoot(HomePage);
                });
            }, error => {
        this.loading.dismiss().then(() => {
                    let alert = this.alertCtrl.create({
                        message: error.message,
                        buttons: [
                            {
                                text: "Ok",
                                role: 'cancel'
                            }
                        ]
                    });
                    alert.present();
                });
      });
            this.toast.create({
            message: 'You have successfully changed your email!',
            duration: 4000
          }).present();
            this.loading = this.loadingCtrl.create();
            this.loading.present();
        }
  changePassword(): void {
  	var user = firebase.auth().currentUser;

	  if (!this.newPassForm.valid) {
            console.log(this.newPassForm.value);
        } else {
        	user.updatePassword(this.newPassForm.value.password).then(user=> {
                this.loading.dismiss().then(() => {
                    this.nav.setRoot(HomePage);
                });
                firebase.database().ref("Students/" + firebase.auth().currentUser.uid).update({
				         password: this.newPassForm.value.password
				  });
            }, error => {
			  this.loading.dismiss().then(() => {
                    let alert = this.alertCtrl.create({
                        message: error.message,
                        buttons: [
                            {
                                text: "Ok",
                                role: 'cancel'
                            }
                        ]
                    });
                    alert.present();
                });
			});
            this.toast.create({
            message: 'You have successfully changed your password!',
            duration: 4000
          }).present();
            this.loading = this.loadingCtrl.create();
            this.loading.present();
        }
        
   }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AccountSettingsPage');
  }

}



